import axios, { AxiosResponse } from 'axios'
import dayjs, { Dayjs } from 'dayjs'
import { useSnackbar } from 'notistack'
import { ChangeEvent, useEffect, useRef, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router-dom'

import MobileDatePicker from '@mui/lab/MobileDatePicker'
import {
  Box,
  Card,
  Checkbox,
  Chip,
  Grid,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography
} from '@mui/material'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import { type Member } from 'src/models/member'
import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'
import { dateParams } from 'src/utils/date'

const ChipHeader = (
  numStack: number,
  numChip: number,
  chipWidth: number,
  levels: string[],
  selectedAllLevel: (level: string) => boolean,
  handleSelectLevelMembers: (level: string) => void
): JSX.Element[] => {
  const chipStack = []
  const levelLength = levels.length
  for (let i = 0; i < numStack; i++) {
    if (i * numChip < levelLength) {
      chipStack.push(
        <Stack direction="row" sx={{ marginBottom: i === numStack - 1 ? 0 : 0.5 }} key={i}>
          {levels.slice(i * numChip, i * numChip + numChip).map(level => {
            return (
              <Chip
                label={level}
                key={level}
                sx={{ marginRight: 0.5, width: `${chipWidth}px` }}
                variant={selectedAllLevel(level) ? 'filled' : 'outlined'}
                onClick={() => handleSelectLevelMembers(level)}
              />
            )
          })}
        </Stack>
      )
    }
  }

  return chipStack
}

const ConferenceRegister = () => {
  const [title, setTitle] = useState<string>('')
  const [date, setDate] = useState<Dayjs | null>(dayjs('2023-01-01T00:00:00'))
  const [members, setMembers] = useState<Member[]>([])
  const [selectedMembers, setSelectedMembers] = useState<number[]>([])
  const [levels, setLevels] = useState<string[]>([])
  const [selectedLevel, setSelectedLevel] = useState<string[]>([])
  const [isPausedListing, setIsPausedListing] = useState<boolean>(true)
  const params = useParams()
  const navigate = useNavigate()
  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()

  const tableRef = useRef<HTMLDivElement>(null)
  const [tableWidth, setTableWidth] = useState(0)
  const CHIP_WIDTH = 87
  const NUM_OF_CHIP_STACK = Math.ceil((levels.length * CHIP_WIDTH) / (tableWidth - 76))
  const NUM_OF_CHIP_PER_STACK = Math.ceil((tableWidth - 76) / CHIP_WIDTH)

  const getTableWidth = () => {
    if (tableRef.current) {
      setTableWidth(tableRef.current.clientWidth)
    }
  }

  const selectedSomeMembers = selectedMembers.length > 0 && selectedMembers.length < members.length
  const selectedAllMembers = selectedMembers.length === members.length

  // 해당 level에 해당하는 모든 member가 선택되었는지 아닌지 체크 -> chip의 variant 변경 시 사용
  const selectedAllLevel = (level: string): boolean => {
    const levelMembers = members.filter((member: Member) => member.level === level).map((member: Member) => member.id)
    // 각 level에 해당하는 모든 member들 중 selected된 member를 제외한 리스트 즉, selected되지 못한 member
    const notSelectedMember = levelMembers.filter(memberId => !selectedMembers.includes(memberId))
    return selectedLevel.includes(level) && !notSelectedMember.length
  }

  const handleSelectAllMembers: React.ChangeEventHandler<HTMLInputElement> = event => {
    setSelectedMembers(event.target.checked ? members && members.map(member => member.id) : [])
    setSelectedLevel(event.target.checked ? levels : [])
  }

  const handleSelectOneMember = (_event: ChangeEvent<HTMLInputElement>, memberId: number): void => {
    const level = members.filter((member: Member) => member.id === memberId).map((member: Member) => member.level)[0]
    if (!selectedMembers.includes(memberId)) {
      setSelectedLevel(prevSelected => Array.from(new Set([...prevSelected, level])))
      setSelectedMembers(prevSelected => [...prevSelected, memberId])
    } else {
      if (
        members.filter((member: Member) => member.level === level && selectedMembers.includes(member.id)).length === 1
      )
        setSelectedLevel(prevSelected => prevSelected.filter(lv => lv !== level))

      setSelectedMembers(prevSelected => prevSelected.filter(id => id !== memberId))
    }
  }

  // 각 level에 해당하는 chip을 선택 또는 해제했을 때 적용
  const handleSelectLevelMembers = (level: string): void => {
    const levelMembers = members.filter((member: Member) => member.level === level).map((member: Member) => member.id)
    if (!selectedLevel.includes(level)) {
      setSelectedLevel(prevSelected => [...prevSelected, level])
      setSelectedMembers(prevSelected => Array.from(new Set([...prevSelected, ...levelMembers])))
    } else {
      setSelectedLevel(prevSelected => prevSelected.filter(lv => lv !== level))
      setSelectedMembers(prevSelected => prevSelected.filter(memberId => !levelMembers.includes(memberId)))
    }
  }

  const handleSubmit = async () => {
    if (title.length > 50) {
      enqueueSnackbar('행사명은 50자 이내로 작성해 주십시오.', { variant: 'error' })
      return
    }
    try {
      const { data } = await axios.post(
        `${process.env.REACT_APP_APIHOST}/club/${params.id}/conference`,
        {
          conference: { clubId: params.id, title: title, startAt: date, user: selectedMembers }
        },
        {
          params: dateParams,
          headers: { Authorization: authHeader() }
        }
      )

      if (data.message === 'ok') {
        enqueueSnackbar('등록이 완료되었습니다.', { variant: 'success' })
        navigate(`/manage/${params.id}/attendance`)
      } else {
        enqueueSnackbar('오류가 발생했습니다. 다시 시도해 주십시오.', { variant: 'error' })
      }
    } catch (e) {
      enqueueSnackbar('오류가 발생했습니다. 다시 시도해 주십시오.', { variant: 'error' })
    }
  }

  const handleIsPausedChange: React.ChangeEventHandler<HTMLInputElement> = e => {
    setIsPausedListing(() => e.target.checked)
  }

  useEffect(() => {
    setTableWidth(tableRef.current?.clientWidth || 450)
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/${params.id}/member`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<Member[]>) => {
        const activeMembers = res.data.filter(member => member.deletedAt === null && member.firedAt === null)
        activeMembers.sort((a, b) => (a.name < b.name ? -1 : 1))
        setMembers(activeMembers)

        const levels = Array.from(
          new Set(activeMembers.filter((member: Member) => member.level !== null).map((member: Member) => member.level))
        ).sort()
        setLevels(levels)
      })
  }, [])

  useEffect(() => {
    window.addEventListener('resize', getTableWidth)
  }, [])

  return (
    <Box m={3}>
      <Helmet>
        <title>출석 관리</title>
      </Helmet>
      <Card>
        <Grid container sx={{ justifyContent: 'center' }}>
          <Grid item xs={12} md={6}>
            <Box padding={{ xs: 3, md: 6 }} paddingBottom={{ xs: 0 }}>
              <Typography variant="h4" color={PureLightTheme.colors.primary.main} sx={{ margin: '20px 0px' }}>
                행사명
              </Typography>
              <TextField required id="title" label="행사명" onChange={e => setTitle(e.target.value)} fullWidth />
              <Typography variant="h4" color={PureLightTheme.colors.primary.main} sx={{ margin: '20px 0px' }}>
                행사일
              </Typography>
              <MobileDatePicker
                label="행사일"
                inputFormat="yyyy/MM/dd"
                value={date}
                onChange={newValue => {
                  setDate(newValue)
                }}
                renderInput={params => <TextField {...params} sx={{ width: '100%' }} />}
              />
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Box padding={{ xs: 3, md: 6 }}>
              <Box display="flex" justifyContent="space-between" alignItems="center" mt={1}>
                <Typography variant="h4" color={PureLightTheme.colors.primary.main} display="inline-block">
                  참여인원
                </Typography>
                <Box>
                  <Typography display="inline-block" variant="body1">
                    휴회원 포함
                  </Typography>
                  <Checkbox sx={{ padding: '5px' }} checked={isPausedListing} onChange={handleIsPausedChange} />
                </Box>
              </Box>

              <TableContainer sx={{ maxHeight: '45vh', overflow: 'scroll' }} ref={tableRef}>
                <Table stickyHeader>
                  <TableHead>
                    <TableRow>
                      <TableCell padding="checkbox">
                        <Checkbox
                          color="primary"
                          checked={selectedAllMembers}
                          indeterminate={selectedSomeMembers}
                          onChange={handleSelectAllMembers}
                        />
                      </TableCell>
                      <TableCell>
                        {levels
                          ? ChipHeader(
                              NUM_OF_CHIP_STACK,
                              NUM_OF_CHIP_PER_STACK,
                              CHIP_WIDTH,
                              levels,
                              selectedAllLevel,
                              handleSelectLevelMembers
                            )
                          : '이름 | 등급'}
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {members &&
                      members.map(member => {
                        if (!isPausedListing && member.isPaused) {
                          return
                        }

                        const isMemberSelected = selectedMembers.includes(member.id)

                        return (
                          <TableRow hover key={member.id} selected={isMemberSelected}>
                            <TableCell padding="checkbox">
                              <Checkbox
                                color="primary"
                                checked={isMemberSelected}
                                onChange={(event: ChangeEvent<HTMLInputElement>): void =>
                                  handleSelectOneMember(event, member.id)
                                }
                                value={isMemberSelected}
                              />
                            </TableCell>
                            <TableCell>
                              <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                                <Box style={{ display: 'flex', alignItems: 'baseline' }}>
                                  {member.level ? `${member.name} | ${member.level}` : member.name}
                                </Box>
                              </Typography>
                              <Typography variant="body2" color="text.secondary" noWrap>
                                {member.studentId}
                              </Typography>
                            </TableCell>
                          </TableRow>
                        )
                      })}
                  </TableBody>
                </Table>
              </TableContainer>
            </Box>
          </Grid>
        </Grid>
        <CancelSubmitBtns handleSubmit={handleSubmit} />
      </Card>
    </Box>
  )
}

export default ConferenceRegister
