import {
  FIRST_SEMESTER,
  FIRST_SEMESTER_MONTH,
  FIRST_SEMESTER_NAME,
  SECOND_SEMESTER,
  SECOND_SEMESTER_MONTH,
  SECOND_SEMESTER_NAME
} from 'src/data/semester'

export const dateParams = {
  semester: window.localStorage.getItem('semester'),
  year: window.localStorage.getItem('year')
}

export interface DateProps {
  semester: Semester
  year: number
}

export type Semester = 'SPRING' | 'FALL'

export const STORAGE_KEY = {
  SEMESTER: 'semester',
  YEAR: 'year'
}

export const getSemesterName = (semester: string): '1학기' | '2학기' => {
  const isFirstSemester = semester === FIRST_SEMESTER
  return isFirstSemester ? FIRST_SEMESTER_NAME : SECOND_SEMESTER_NAME
}

export const getCurrentSemester = (): Semester => {
  const today = new Date()
  const currentMonth = today.getMonth()
  const isFirstSemester = FIRST_SEMESTER_MONTH <= currentMonth && currentMonth < SECOND_SEMESTER_MONTH
  return isFirstSemester ? FIRST_SEMESTER : SECOND_SEMESTER
}

export const getCurrentYear = () => {
  return new Date().getFullYear()
}
